*{
    margin: 0;
    padding: 0;
}
.App-input{
    text-align: center;
    background-color: lightblue;
    height: 300px;
    color: navy;
    font-size: 18px;
}
.App-input h1{
    padding-top: 20px;
}
input{
    font-size: 14px;
    border-radius: 5px;
    border: 1px solid gray;
    outline: 0;
    padding: 10px 5px;
    width: 200px;
}
.App-input button{
    background-color: lightskyblue;
    color: white;
    padding: 10px 20px;
    border-radius: 30px;
    margin-top: 10px;
}
.navActive{
    color: burlywood;
}
nav{
    
    background-color: blueviolet;
    padding: 10px 0;
    display: flex;
    justify-content: center;
    margin-top: 5px;
}
nav ul{
    display: flex;
    justify-content: space-around;
    list-style: none;
    align-items: center;
    width: 100%;
    color: white;
    font-weight: bold;
}
nav ul li{
    cursor: pointer;
    font-size: 16px;
    transition: all 0.5s ease-in-out;
}
nav ul li:hover{
    transform: scale(1.1);
}
h3{
    margin: 5px 10px;
}
img{
    margin: 5px 10px;
    height: 100px;
    width: 100px;
    border-radius: 50%;
    border: 1px solid wheat;
}